@import './../../var';

.search {
  input, label {
    display: block;
  }
  label {
    border-bottom: 1px solid $mainBgColor;
  }
  input {
    width: 100%;
    padding: 1rem;
    border: none;
    background-color: transparent;
    outline: none;
    color: white;
    font-size: 1.2rem;
    &:active {
      outline: none;
    }
  }
}