@import './../../var';

.movie {
  width: 250px;
  margin: 1rem;
  border-radius: 3px;
  box-shadow: 3px 3px 10px rgba(black, .3);
  position: relative;
  overflow: hidden;
  &:hover {
    .movie__over {
      transform: translateY(0);
    }
  }
  &__header {
    display: flex;
    flex-direction: column;
    height: 100%;
  }
  &__poster {}
  &__info {
    padding: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: $secondBgColor;
    height: 100%;
    h3 {
      margin-right: 10px;
    }
    span {
      display: block;
    }
  }
  &__vote {
    padding: 0.3rem 1rem;
    background: darken($secondBgColor, 5);
    font-weight: bold;
    border-radius: 3px;
    &-green {
      color: seagreen;
    }
    &-orange {
      color: orange;
    }
    &-red {
      color: tomato;
    }
  }
  &__over {
    background-color: rgba(white, .9);
    color: $overTxtColor;
    position: absolute;
    bottom: 0;
    top: 0;
    left: 0;
    right: 0;
    padding: 1rem;
    transform: translateY(100%);
    transition: transform .3s;
    overflow: auto;
    h2 {
      margin-bottom: 1rem;
    }
    p {}
  }
}